.SurveySection {
  height: 350px;
  background-color: #a3cbd5;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.SurveySection h1 {
  margin: 5px;
  font-size: 40px;
}

.SurveySection button {
  margin-top: 30px;
  background-color: black;
  width: 250px;
  height: 80px;
  border-radius: 40px;
  cursor: pointer;

  font-size: 17px;
  font-weight: bold;
  color: white;
}
