.ThirdMenuBox {
  display: flex;
  width: auto;
  height: auto;
  border: 1px solid white;
  padding: 10px;
  box-shadow: 1px 4px 5px grey;
  border-radius: 2%;
}

.isHided {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.isHided p {
  font-size: 23px;
  font-weight: bold;
  user-select: none;
}

.isOpened {
  border-top: 0.5px solid gray;
}

.Hide-menu {
  height: 70px;
  overflow: hidden;
}
